
@import '../../../static/highligh/styles/default.css';
h1,
h2 {
  color: #00bcd4;
  font-weight: normal;
  position: relative;
  margin: 5px 0;
}
h1 {
  font-size: 26px;
}
h1:hover:before {
  content: '#';
  position: absolute;
  left: -15px;
  top: 5px;
}
h2 {
  font-size: 23px;
}
h2:hover:before {
  content: '#';
  position: absolute;
  left: -13px;
  top: 5px;
}
blockquote {
  position: relative;
  padding: 15px 20px;
  border-left: 4px solid #00cfe1;
  color: #6e6e6e;
  background: #f5f5f5;
  font-size: 14px;
  border-radius: 0 2px 2px 0;
  margin-left: 0;
}
blockquote p {
  font-size: 16px;
  margin: 0;
}
.article-container {
  box-sizing: border-box;
  position: relative;
  max-width: 700px;
  box-shadow: 0 0 20px rgba(0,0,0,0.3);
  border-radius: 4px;
}
.article-container .article {
  padding: 35px 35px 10px 35px;
  min-height: 750px;
  background: #fff;
  font-family: -apple-system, SF UI Text, Arial, PingFang SC, Hiragino Sans GB, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
}
.article-container .article .artice-img-wrapper {
  overflow-x: auto;
}
.article-container .info {
  background: #fff;
  border-top: 1px solid #d4d4d4;
  padding: 0 20px;
  height: 40px;
}
.article-container .info .article-cate {
  padding: 1px 5px;
  font-size: 20px;
  border-radius: 3px;
  line-height: 40px;
  background: #8bc34a;
  color: #fff;
}
.article-container .info .article-cate:hover {
  cursor: pointer;
}
.article-container .info .article-time {
  float: right;
  line-height: 40px;
  font-size: 18px;
  font-weight: normal;
  color: #727272;
}
.article-container .info .icon-eye {
  margin-left: 10px;
  font-size: 16px;
  line-height: 16px;
  color: #949494;
}
.article-container .info .pv {
  position: relative;
  bottom: 1px;
  font-size: 16px;
  line-height: 16px;
  color: #949494;
}


