
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.flash {
  position: fixed;
  box-sizing: border-box;
  margin-left: -52px;
  left: 50%;
  top: 200px;
  width: 104px;
  z-index: 100;
  padding: 12px;
  font-size: 20px;
  line-height: 20px;
  border-radius: 10px;
  color: #fff;
}
.flash.log-successed {
  background: #27abf9;
}
.flash.log-failed {
  background: #f00;
}
.overview {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.3);
  z-index: 50;
}
.login-wrapper {
  position: fixed;
  z-index: 100;
  left: 50%;
  top: 40%;
  width: 315px;
  height: 160px;
  margin-left: -157px;
  margin-top: -80px;
}
.login-wrapper .login_form {
  background: #fff;
  padding: 10px;
  border-radius: 10px;
}
.login-wrapper .login_form .item {
  display: block;
  border: 1px solid #808080;
  border-radius: 5px;
  background: #f3f4f5;
  padding: 5px;
  width: 280px;
  margin: 20px auto;
}
.login-wrapper .login_form .submit {
  margin: 10px auto 0;
  background: #86ce2f;
}
.login-wrapper .login_form .submit:hover {
  cursor: pointer;
}
.login-wrapper .login_form .close {
  position: absolute;
  right: 5px;
  top: -5px;
  padding: 5px;
}
.login-wrapper .login_form .close:hover {
  cursor: pointer;
}


