
@import './common/fonticon/style.css';
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background-color: #dae4e5;
}
::-webkit-scrollbar-thumb {
  background-color: #00cfe1;
}
.faden-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.faden-enter,
.fade-leave-to {
  opacity: 0;
}
body {
  margin: 0;
  background: #f6f6f6;
}
body .header {
  position: fixed;
  z-index: 100;
  width: 100%;
  top: 0;
  left: 0;
}
body .menu-container {
  position: fixed;
  z-index: 50;
}
body .view {
  margin: 70px auto 0;
}
body .up-wrapper {
  position: fixed;
  bottom: 60px;
  right: 10px;
}


